html {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

ul, li, a {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.nav__list {
  display: flex;
}

.nav__item {
  height: 60px;
  display: flex;
}

.nav__list > .nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  transition: all .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
}

.header__logo-img {
  width: 100%;
  height: 100%;
}

.stars {
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--0 .stars__star:nth-child(n), .stars--1 .stars__star:nth-child(n+2), .stars--2 .stars__star:nth-child(n+3), .stars--3 .stars__star:nth-child(n+4), .stars--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover > .card__title {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-count {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-weight: 400;
}

.card__price-content {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.cards {
  grid-template-columns: 1fr;
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  max-width: 944px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.main {
  flex-direction: column;
  align-items: center;
  display: flex;
